@import url(https://fonts.googleapis.com/css2?family=Kaisei+Tokumin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary-white : #fff;
    --primary-blue : #4C43D4;
    --primary-dark : rgba(255,255,255,0);
    --primary-orange : #f9ab00;
}

body{
    font-family: 'Kaisei Tokumin', serif;
}


.navbar-custom {
    background-color: #191a2b;
    
}

.navbar-light .navbar-toggler {
    color: white;
    border-color: white;
}

.logo{
    height: 3.125rem;
    width: 4.375rem;
}

nav a.nav-link {
    font-weight: 200;
    letter-spacing: 0.0625rem;
    cursor: pointer;
}

nav ul li {
    
    text-transform: uppercase;
    
}
.navbar-nav{
    margin-left: auto;
}

.navbar-nav > li > a {
    margin-left: 0.625rem;
    margin-right: 0.625rem;
    color: azure !important;
    border-bottom: 0.1875rem solid rgba(255,255,255,0);
    border-bottom: 0.1875rem solid var(--primary-dark) ;
}

.navbar-nav > li > a:focus {
    border-bottom: 0.1875rem solid #4C43D4;
    border-bottom: 0.1875rem solid var(--primary-blue);
}

.navbar-nav > li > a:hover {
    border-bottom: 0.1875rem solid #4C43D4;
    border-bottom: 0.1875rem solid var(--primary-blue);
    color: sandybrown !important;
}

/********************Header*************************/

.bg-image {
    
    position: relative;
    background: #1a1c2b url(/portfolio/static/media/background.f3253abd.jpg) no-repeat;
    background-size: contain;
    background-position:center;
    height:90vh;
    overflow: hidden;
    
   

   
}
.bg-text  {

  background-color: rgb(0,0,0) ;
  background-color: rgba(0,0,0, 0.65) ; 
  color: #f9ab00; 
  color: var(--primary-orange) ;
  text-transform: uppercase ;
  font-weight: bold ;
  border: 3px solid #f1f1f1 ;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family:'Cormorant Garamond', serif;
  z-index: 2;
  height: 20vh;
  width: 60vw ; 
  padding: 20px 0 20px 0;
  
  text-align: center;
   justify-content: center;
 

    
}
.typed-text {
    text-transform: capitalize;
    color: white;
    letter-spacing: 1.5px;
}



.tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}
nav{
    z-index: 2;
}

/********************Common Update*************************/



.title_heading {
    text-align: center;
    text-shadow: 2px 2px 4px red;
    color: wheat;
}

.about__main {
    background-color: #1a1c2b;
    height: 95vh;
}
h1.aboutme-tite{
    text-align: center;
    color: whitesmoke;
    text-decoration: underline;
}
.pic-container .img-fluid{
    border: #f9ab00 solid 0.5px;
    border-radius: 30px;
    box-shadow: #0011f9 0 25px 80px -15px;
    height: 50%;
    width: auto;
}

.pic-container{
    display: flex;
    justify-content: center;
    padding-top: 40px;
}

.navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 165, 0, 0.8%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}
.navbar-light .navbar-toggler {
    border-color:orange;
    border-radius: 2px;
    
}

h1.about__heading{
    margin-top: 30px;
    font-size: 50px;
    color: #f9ab00;
    text-align: left;
}

h1.about__heading:hover{
    letter-spacing: 3px;
    color: red;
}

.about_content {
    padding-top: 50px;
    padding-bottom: 0;
}
.name{
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 1px;
    
    
}
.CVbtn{
    background-color:#1a1c2b;
    color: white;
    padding: 9px;
    border: white solid 1px;
    border-radius: 15px;
    font-weight: 500;
}
.CVViewbtn{
    background-color: #1a1c2b;
    color: white;
    padding: 9px 30px;
    margin: 0px 20px;
    border: white solid 1px;
    border-radius: 15px;
    font-weight: 500;
}
.CVViewbtn:hover {
    background-color: aliceblue;
    color: #0011f9;
}
.CVbtn:hover {
    background-color: aliceblue;
    color: #0011f9;
}
h3{
    color: whitesmoke;
    text-align: start;
   
}
p{
    color: whitesmoke;
    line-height: 35px;
    font-size: 20px;
}

@media screen and (max-width:576px) {
    .pic-container .img-fluid {
        height: auto;
        width: 75%;
    }
}
@media screen and (max-width:767px) {
    .about__main {
        height: auto;
    }
    .CVbtn{
        display: none;
    }
    
    .about__heading{
        display: none;
    }
    .pic-container{
        padding-bottom: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    p{
        color: whitesmoke;
        line-height: 30px;
        font-size: 18px;
    }
}
@media screen and (min-width: 576px) and (max-width:1200px) {
    
    .img-fluid {
        height: auto;
        width: auto;
    }
    
}



