.contact_main {
    background-color: #23242e;
    height: 50vh;
}

.contact_content {
    padding-top: 30px;
    margin-bottom: 30px;
    margin-left: 90px;
    padding: 10px 10px;
    position: relative;
    float: left;

}

.contact_content .logo {
    height: 5%;
    width: 5%;
}

@media screen and (max-width:480px) {
    .contact_main {
        height: 70vh;
    }
    .contact_content {
            padding-top: 30px;
            margin-bottom: 30px;
            margin-left: 90px;
            padding: 10px 10px;
            position: relative;
            
        
    
        margin-left: 20px;
    }
    .contact_content h3 {
        float:right;
        margin-top: 0;
        margin-bottom: 0;
        margin-right: auto;
        margin-left: auto;
        padding: 15px 50px;
        text-align: center;
    }
    .contact_content .logo {
        margin-left: 0px;
        height: 20%;
        width: 20%;
    }
}

@media screen and (min-width:481px) and (max-width:767px) {
    .contact_content {
        margin-left: 20px;
    }
    .contact_content h3 {
        float:right;
        margin-top: 0;
        margin-bottom: 0;
        margin-right: auto;
        margin-left: auto;
        padding: 15px 110px;
        text-align: center;
    }
    .contact_content .logo {
        margin-left: 0px;
        height: 15%;
        width: 15%;
    }
}

@media screen and (min-width:1280px) {
    .test{
        display:flex;
        float: left;
        align-items: center;
        justify-content: center;
        padding-left: 200px;


    }
    .contact_content .logo {
        height: auto;
        width: 30%;
    }
    .contact_content .logo:hover {
        height: auto;
        width: 50%;
    }

}
.project_main{
    background-color: #1a1c2b;
    height: auto;
}
.project_main .title_heading {
    padding-top: 50px;
    padding-bottom: 50px;
}
.project_img {
    display:flex;
    align-items:center;
    justify-content: center;
}

.img-fluid {
    height: auto;
    width: 95%;
}
.card-section {
    display: flex;
    justify-content: start;
    padding-bottom: 30px;
}

.card {
    box-shadow:5px 10px 15px blue;
}
.card-section:nth-child(odd) {
    justify-content: end;
}
.card-title {
    font-size: 25px;
    text-align: center;
    font-weight: 700;
}
.card-text {
    color: #1a1c2b;
    line-height: normal;
}

#projects .item {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 15rem;
}
#projects .item:nth-child(even){
    flex-direction: row-reverse;
}
#projects .item .left {
    flex: 1 1;
}
#projects .item .right {
    flex: 1 1;
    margin-left: -150px;
}
#projects .item {
    height: 50%;
    width: 50%;
}
.project-desc {
    color: black;
}
#projects .img {
    height: auto;
    width: 50%;
}



@media screen and (max-width: 576px) {
.img-fluid {
    height: auto;
    width: 100%;
}
}
